import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  faSortDown } from '@fortawesome/free-solid-svg-icons'
import { format } from 'date-fns';

const Dates = () => {
  let today = new Date();

  return (
    <div className="bg-gray-900 text-white px-4 py-2 mt-5">
      <div className="flex items-baseline justify-start mb-2">
        <FontAwesomeIcon icon={faSortDown} style={{ fontSize: 28 }} />
        <span className="ml-1 font-alt text-2xl font-bold">SELECIONE UMA DATA</span>
      </div>
      <div>
        <a onClick={() => { }} href="#" className="block font-alt py-1">Todas</a>
        <a onClick={() => { }} href="#" className="block font-alt py-1">Jogos de Hoje {format(new Date(), 'dd/MM/yyyy')}</a>
        <a onClick={() => { }} href="#" className="block font-alt py-1">Jogos de Amanhã {format(today.setDate(today.getDate() + 1), 'dd/MM/yyyy')}</a>
        <a onClick={() => { }} href="#" className="block font-alt py-1">Jogos do dia {format(today.setDate(today.getDate() + 1), 'dd/MM/yyyy')}</a>
      </div>
    </div>
  );
}

export default Dates