import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSignInAlt } from '@fortawesome/free-solid-svg-icons'

const Header = () => (
  <header className="">
    <div className="bg-brand">
      <div className="container mx-auto px-4 lg:px-0">
        <div className="w-100 flex items-center justify-between">
          <p className="text-white text-xs">2021 - Cuia Sports</p>
          <button type="button" className="transform -skew-x-12 px-5 py-1 mt-1 -mb-1 lg:mr-0 outline-none focus:outline-none bg-white z-10">
            <div className="transform skew-x-12 text-sm text-brand">
              Entrar <FontAwesomeIcon icon={faSignInAlt} />
            </div>
          </button>
        </div>
      </div>
    </div>
    <div className="header-middle">
      <div className="container mx-auto">
        <div className="w-100 flex items-center justify-center md:justify-between">
          <div className="hidden md:flex justify-start items-center flex-1 gap-7">
            <a href="#">INÍCIO</a>
            <a href="#">REGRAS</a>
          </div>
          <div className="pt-2">
            <a href="/" title="CUIA Sports" className="flex items-center justify-center flex-col">
              <img src="/logo.png" alt="CUIA Sports" className="max-h-28 md:max-h-40 flex-none pr-3" />
              <div className="text-brand-40 text-sm font-bold text-shadow">A sua sorte está aqui</div>
            </a>
          </div>
          <div className="hidden md:flex justify-end items-center flex-1 gap-7">
            <a href="#">RESULTADOS</a>
            <a href="#">CONTATO</a>
          </div>
        </div>
      </div>
    </div>
  </header>
)

export default Header