import MainLayout from "../layouts/MainLayout";
import Sidebar from "./Sidebar";
import Matches from "./Matches";
import Banners from "./Banners";

export default function Home() {
  return (
    <MainLayout>
      <main className="container mx-auto mt-6">
        <div className="w-100 flex-none flex flex-col lg:flex-row">
          <aside className="flex flex-col lg:w-4/12 lg:pr-7 order-2 lg:order-1">
            <Sidebar />
          </aside>
          <div className="flex-1 order-1 lg:order-2 mb-8">
            {/* <Banners /> */}
            <Matches />
          </div>
        </div>
      </main>
    </MainLayout>
  );
}