import React, { useEffect, useState } from 'react'
import getCampeonatos from '../services/champions-bet';
import dateTimeToString from '../utils/utils';
import Loading from './Loading';
import Modal from './ui/Modal';

export default function Matches() {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([] as any);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    getCampeonatos().then((res) => {
      setData(res);
      setLoading(false);
    });
  }, []);

  const findCotacaoValue = (cotacoes: any[], chave: string): Number => {
    const item = cotacoes.find((x) => x.chave === chave);

    if (item)
      return item.valor as Number;

    return 0;
  }

  function handleOddSelect(event: any) {
    if (!event.target.classList.contains("selected")) {
      event.target.parentNode.childNodes.forEach((node: any) => {
        if (node.classList.contains("selected")) {
          node.classList.remove("selected")
        }
      });

      event.target.classList.add("selected");
    }
    else
      event.target.classList.remove("selected");
  }

  if (loading) {
    return <Loading />;
  }

  if (!data || data.length === 0) {
    return (
      <section id="matches font-alt">
        <p className="text-white text-center">Nenhum evento disponível...</p>
      </section>
    );
  }

  return <>
    <section id="matches" className="font-alt">
      {data.map((camp: any) => (
        <div key={camp._id} className="mb-8">
          <div className="px-3 pt-2 pb-1 border-b-2 bg-brand-60 border-brand-40 text-white font-semibold shadow">{camp.nome}</div>
          <div className="flex justify-center lg:justify-end text-sm text-brand-40 font-light">
            <div className="flex items-center justify-between lg:justify-end w-11/12 lg:w-80 mb-2 lg:mb-0">
              <div className="w-1/4 p-2 text-center">Casa</div>
              <div className="w-1/4 p-2 text-center">Empate</div>
              <div className="w-1/4 p-2 text-center">Fora</div>
              <div className="w-1/4 p-2 text-center">+</div>
            </div>
          </div>
          <div className="lg:pl-3">
            {camp.jogos.map((jogo: any) => (
              <div key={jogo._id} className="flex flex-col lg:flex-row lg:justify-between items-center mb-6 lg:mb-2">
                <div className="flex flex-col lg:block justify-center items-center text-white text-sm md:text-base">
                  {jogo.nome}
                  <span className="ml-2 text-gray-600">{dateTimeToString(jogo.horario)}</span>
                </div>
                <div className="flex w-11/12 lg:w-80 flex-shrink-0 justify-between text-white text-sm rounded-md overflow-hidden cursor-pointer" style={{ backgroundColor: '#1A212A' }}>
                  <div className="odd w-1/4 p-2 text-center hover:bg-brand-40 hover:text-black transition-all ease-in" onClick={handleOddSelect}>{findCotacaoValue(jogo.cotacoes, "casa_90")}</div>
                  <div className="odd w-1/4 p-2 text-center hover:bg-brand-40 hover:text-black transition-all ease-in" onClick={handleOddSelect}>{findCotacaoValue(jogo.cotacoes, "empate_90")}</div>
                  <div className="odd w-1/4 p-2 text-center hover:bg-brand-40 hover:text-black transition-all ease-in" onClick={handleOddSelect}>{findCotacaoValue(jogo.cotacoes, "fora_90")}</div>
                  <div className="odd more-odds w-1/4 p-2 text-center text-brand-40 hover:bg-brand-40 hover:text-black transition-all ease-in"
                    onClick={() => setOpen(true)}>+{jogo.total_cotacoes}</div>
                </div>
              </div>
            ))}
          </div>
        </div>
      ))}
    </section>
    <Modal open={open} setOpen={setOpen}>
      <Loading />
    </Modal>
  </>
}
