import axios from 'axios';
import { format } from 'date-fns';

async function getParametros() {
  var now = new Date();
  // now.setHours(now.getHours() + 1);
  return await axios.get(`http://usadonacidade.com.br/robots.php?url=http://www.championsbet.club/param/parametros.json? ${now.getTime()}`)
    .then((res) => res.data)
    .catch((error) => { });
}

async function getCampeonatos() {
  const parametros = await getParametros();
  const campeonatos_bloqueados = parametros.campeonatos_bloqueados || [];

  return await axios.get('https://center2.wee.bet/v1/campeonatos', {
    params: {
      'sport_id': 1,
      'campeonatos_bloqueados': campeonatos_bloqueados.join(','),
      'odds': 'casa_90,empate_90,fora_90',
      // 'data': format(new Date(), "yyyy-MM-dd")
      'data': '2021-05-26'
    }
  })
    .then((res) => {
      let campeonatos = res.data.result;

      const libertadores = campeonatos.filter((a: any) => a.nome.includes("LIBERTADORES"));
      campeonatos = campeonatos.filter((a: any) => !a.nome.includes("LIBERTADORES"));

      const brasileiros = campeonatos.filter((a: any) => a.nome.includes("BRASIL: JOGOS BRASILEIROS"));
      campeonatos = campeonatos.filter((a: any) => !a.nome.includes("BRASIL: JOGOS BRASILEIROS"));

      const brasil = campeonatos.filter((a: any) => a.nome.includes("BRASIL:"));
      campeonatos = campeonatos.filter((a: any) => !a.nome.includes("BRASIL:"));


      return [
        ...libertadores,
        ...brasileiros,
        ...brasil,
        ...campeonatos
      ];
    })
    .catch((error) => {
      return []
    });
}
export default getCampeonatos;