import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircleNotch } from '@fortawesome/free-solid-svg-icons'

export default function Loading({ size = 32 }) {
  return (
    <div style={{ display: 'flex', alignSelf: 'center', justifyContent: 'center', margin: '30px 0' }}>
      <FontAwesomeIcon
        className="loading-icon text-brand-40 animate-spin"
        icon={faCircleNotch}
        style={{
          fontSize: `${size}px`
        }}
      />
    </div>
  )
}
