import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  faSortDown } from '@fortawesome/free-solid-svg-icons'

const Leagues = () => {
  return (
    <div className="bg-gray-900 text-white px-4 py-2 mt-5">
      <div className="flex items-baseline justify-start mb-2">
        <FontAwesomeIcon icon={faSortDown} style={{ fontSize: 28 }} />
        <span className="ml-1 font-alt text-2xl font-bold">LIGAS</span>
      </div>
      <div>
        <span className="flex items-center font-alt py-1">Alemanha Bundesliga <span className="ml-2 bg-gray-400 text-white text-xs rounded-lg px-2">4</span></span>
      </div>
    </div>
  );
};

export default Leagues