import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBasketballBall, faTableTennis } from '@fortawesome/free-solid-svg-icons'
import { GiBoxingGlove } from "react-icons/gi";
import { IoFootballSharp } from "react-icons/io5";

const SPORT_TYPES = [
  {
    id: 1,
    name: "Futebol",
    icon: <IoFootballSharp fontSize={28} />,
  },
  {
    id: 2,
    name: "Basquete",
    icon: <FontAwesomeIcon icon={faBasketballBall} style={{ fontSize: 24 }} />,
  },
  {
    id: 3,
    name: "Combate",
    icon: <GiBoxingGlove fontSize={26} />,
  },
  {
    id: 4,
    name: "Tênis",
    icon: <FontAwesomeIcon icon={faTableTennis} style={{ fontSize: 24 }} />,
  },
]

const Sports = () => {
  return (
    <div className="flex flex-wrap justify-center">
      {SPORT_TYPES.map((item) => (
        <div key={item.id} className="cursor-pointer group">
          <div className="sports-item group-hover:opacity-80">
            {item.icon}
          </div>
          <span className="group-hover:opacity-80 block text-center text-white mt-2 font-semibold uppercase" style={{ fontSize: '0.65rem' }}>{item.name}</span>
        </div>
      ))}
    </div>
  );
};

export default Sports